import showsubjectiveFrom from '../../components/showSubjective_problem.vue';
export default {
  data: function data() {
    return {
      data: null,
      currentPage4: 1,
      input3: '',
      select: '',
      loading: false,
      FormTitle: '',
      visible: false
    };
  },
  components: {
    showsubjectiveFrom: showsubjectiveFrom
  },
  methods: {
    handleEdit: function handleEdit(index, row) {
      this.FormTitle = '查看主观题';
      this.$store.state.isFromShow = true;
      this.$store.state.SubjectiveFormShow = true;
      this.data = row;
    },
    freshTableData: function freshTableData() {
      this.$store.commit('changeNowQuestionType', 'subjective_publish');
      this.$store.dispatch('get_PageInfo_AJAX');
      this.$store.dispatch('get_listData_AJAX');
    },
    handleDelete: function handleDelete(index, row) {
      console.log(index, row);
    },
    handleCurrentChange: function handleCurrentChange(val) {
      this.$store.commit('changePageInfo', val);
      this.$store.dispatch('get_PageInfo_AJAX');
      this.$store.dispatch('get_listData_AJAX');
    },
    mousemove: function mousemove(event) {
      var event1 = event.currentTarget;
      var event2 = event.target;
      var dialogBox = document.getElementById('dialogBodybox');
      var leftcha = event.clientX - dialogBox.offsetLeft;
      var topcha = event.clientY - dialogBox.offsetTop;

      if (event1 == event2) {
        document.onmousemove = function (eventmove) {
          var left = eventmove.clientX - leftcha;
          var top = eventmove.clientY - topcha;
          dialogBox.style.left = left + 'px';
          dialogBox.style.top = top + 'px';

          document.onmouseup = function () {
            document.onmousemove = null;
            document.onmouseup = null;
          };
        };
      }
    },
    close_doalog: function close_doalog() {
      this.$store.state.isFromShow = false;
    },
    stop: function stop() {
      console.log('stop');
    },
    getSubjectiveData: function getSubjectiveData(data) {
      console.log('downdata', data);
      this.$store.commit('getUpdateData', data);
      this.$store.dispatch('update_listData_AJAX');
    }
  },
  beforeDestroy: function beforeDestroy() {
    this.$store.state.isFromShow = false;
  }
};